@font-face {
  font-family: 'Gilroy';
  src: url("../fonts/gilroy/Gilroy-Medium.woff2") format("woff2"), url("../fonts/gilroy/Gilroy-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Gilroy';
  src: url("../fonts/gilroy/Gilroy-Bold.woff2") format("woff2"), url("../fonts/gilroy/Gilroy-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

:root {
  --content-width: 944px;
  --gutter: 30px;
  --mob-padding: 26px;
}

.container {
  max-width: var(--content-width);
  padding-left: var(--mob-padding);
  padding-right: var(--mob-padding);
  margin-left: auto;
  margin-right: auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: calc(var(--gutter) * -1);
  margin-right: calc(var(--gutter) * -1);
}

html {
  font-family: 'Gilroy';
  font-size: 18px;
}

body.content-hidden {
  overflow: hidden;
}

img {
  display: block;
  max-width: 100%;
}

.burger {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 33px;
  height: 20px;
  cursor: pointer;
  color: var(--txt-dark);
  z-index: 9;
}

.burger_closed {
  top: 68%;
}

.burger_closed .burger__line {
  background: var(--txt-light);
}

.burger_closed .burger__line:nth-child(1) {
  transform: translate(0, 4px) rotate(45deg);
}

.burger_closed .burger__line:nth-child(2) {
  display: none;
}

.burger_closed .burger__line:nth-child(3) {
  transform: translate(0, -12px) rotate(-45deg);
}

.burger__line {
  height: 4px;
  background: var(--txt-dark);
  transition: 0.3s;
}

.lang-switch__items {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.lang-switch__item {
  font-size: 34px;
  padding-left: 5px;
  padding-right: 5px;
  line-height: 1;
  font-weight: bold;
  border-right: 4px solid #000;
}

.lang-switch__item:last-child {
  border-right: none;
}

.lang-switch__link {
  text-decoration: none;
  color: var(--txt-light);
}

.s-title {
  text-align: center;
  color: #000000;
  font-size: 34px;
  line-height: 1.2;
}

.rate__star {
  display: inline-block;
  width: 26px;
  height: 26px;
  margin-right: 4px;
  margin-left: 4px;
  background-image: var(--empty-rate);
  background-repeat: no-repeat;
  background-size: inherit;
}

.rate[data-mark="5"] .rate__star:nth-child(-n+5) {
  background-image: var(--star-rate);
}

.rate[data-mark="4"] .rate__star:nth-child(-n+4) {
  background-image: var(--star-rate);
}

.rate[data-mark="3"] .rate__star:nth-child(-n+3) {
  background-image: var(--star-rate);
}

.rate[data-mark="2"] .rate__star:nth-child(-n+2) {
  background-image: var(--star-rate);
}

.rate[data-mark="1"] .rate__star:nth-child(-n+1) {
  background-image: var(--star-rate);
}

.btn {
  display: inline-block;
  align-items: center;
  padding: 12px 97px;
  background-color: #000;
  border-radius: 50px;
  text-align: center;
  text-decoration: none;
  color: white;
  font-size: 18px;
  line-height: 22px;
  appearance: none;
  cursor: pointer;
}

.header__inner {
  padding-top: 65px;
  position: relative;
}

.header__menu-container {
  display: none;
}

.header__menu-container_visible {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  padding-top: 56px;
  padding-left: 26px;
  background-color: var(--bg-light);
  z-index: 9;
}

.header__menu-container_visible .lang-switch {
  display: block;
  z-index: 10;
}

.header__nav {
  overflow-y: auto;
  height: 100%;
}

.header__menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.header__menu-item {
  text-decoration: none;
  color: var(--txt-light);
  margin-bottom: 65px;
  max-width: 80%;
  font-size: 34px;
  line-height: 1.2;
  font-weight: bold;
}

.header__lang-switch {
  display: none;
  position: absolute;
  z-index: 15;
}

.header__lang-switch_visible {
  display: block;
  margin-right: -50px;
  top: 900%;
  right: 0;
}

.header__langs {
  transform: rotate(90deg);
}

.header__burger {
  position: absolute;
  right: 0;
  bottom: -45%;
}

.promo {
  padding-bottom: 83px;
}

.promo__info-block {
  position: relative;
}

.promo__author {
  margin: 0;
  margin-bottom: 15px;
  font-size: 47px;
  line-height: 1.3;
  font-weight: bold;
}

.promo__info {
  margin: 0;
  margin-bottom: 36px;
  line-height: 1.2;
}

.promo__lang-switch {
  display: none;
  position: absolute;
  top: 49%;
  right: 0;
}

.promo__langs {
  transform: rotate(270deg);
  margin-right: -32px;
}

.promo__lang-item {
  font-size: 16px;
}

.promo__img-block {
  max-height: 478px;
  overflow: hidden;
}

.promo__img {
  position: relative;
}

.about {
  padding-top: 80px;
  padding-bottom: 75px;
  background-color: var(--bg-dark);
}

.about__inner {
  max-width: 315px;
  margin-left: auto;
  margin-right: auto;
}

.about__title {
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin-bottom: 30px;
}

.about__desc {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: center;
  margin-bottom: 40px;
  line-height: 1.3;
  font-size: 18px;
}

.about__desc:last-child {
  margin-bottom: 0;
}

.skills {
  padding-top: 80px;
  padding-bottom: 80px;
}

.skills__title {
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin-bottom: 39px;
}

.skills__subtitle {
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  margin-bottom: 50px;
}

.skills__card {
  padding-top: 25px;
  padding-right: 20px;
  padding-bottom: 25px;
  padding-left: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.skills__card:last-child {
  margin-bottom: 0;
}

.skills__card-img {
  max-height: 85px;
  margin-bottom: 12px;
}

.skills__card-title {
  margin-bottom: 40px;
  max-width: 80px;
  text-align: center;
  color: var(--txt-light);
  font-size: 14px;
  line-height: 17px;
}

.skills__card-rate {
  margin-top: auto;
}

.portfolio {
  padding-top: 80px;
  padding-bottom: 80px;
  background-color: var(--bg-dark);
}

.portfolio__title {
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin-bottom: 65px;
}

.portfolio__card {
  display: flex;
  flex-direction: column;
  margin-bottom: 75px;
}

.portfolio__card:last-child {
  margin-bottom: 0;
}

.portfolio__img {
  margin-bottom: 33px;
}

.portfolio__link {
  text-decoration-line: underline;
  text-align: center;
  color: #000;
  font-size: 18px;
  line-height: 22px;
}

.footer {
  padding-top: 80px;
  padding-bottom: 90px;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.footer__title {
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin-bottom: 25px;
}

.footer__subtitle {
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin-bottom: 40px;
  text-align: center;
  color: #090909;
  font-size: 18px;
  line-height: 22px;
}

.footer__btn {
  margin-bottom: 48px;
}

.footer__icons-wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 400px;
  margin-bottom: 25px;
}

.footer__link {
  text-decoration: none;
  margin-right: 10px;
  margin-bottom: 20px;
  margin-left: 10px;
}

.footer__icon {
  max-width: 40px;
}

.footer__copyright {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: center;
  color: var(--txt-light);
  font-size: 18px;
  line-height: 22px;
}

@media (min-width: 640px) {
  .promo__img-block {
    max-height: 387px;
  }

  .promo__img {
    bottom: 270px;
  }

  .skills__subtitle {
    margin-bottom: 70px;
  }

  .skills__card-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  .skills__card {
    margin-bottom: 0;
  }

  .portfolio {
    padding-top: 110px;
  }

  .portfolio__card {
    margin-bottom: 100px;
  }
}

@media (min-width: 992px) {
  .burger {
    display: none;
  }

  .header__inner {
    padding-bottom: 30px;
  }

  .header__menu-container {
    display: block;
  }

  .header__menu {
    display: flex;
    justify-content: space-between;
  }

  .header__menu-item {
    font-size: 18px;
  }

  .header__burger {
    display: none;
  }

  .promo__info-block {
    display: flex;
    align-items: flex-end;
  }

  .promo__author {
    margin-right: 29%;
  }

  .promo__info {
    margin-bottom: 27px;
  }

  .promo__lang-switch {
    display: block;
  }

  .promo__img {
    bottom: 400px;
  }

  .about {
    padding-top: 115px;
    padding-bottom: 107px;
  }

  .about__inner {
    max-width: 485px;
  }

  .footer {
    padding-top: 115px;
    padding-bottom: 120px;
  }

  .footer__btn {
    margin-bottom: 70px;
    padding: 12px 28px;
  }
}